'use strict'

export default function (router) {
	router.map({
		'/': {
			name: 'home',
			component: (resolve) => {
				require(['./views/index/app.vue'], resolve)
			},
			subRoutes: {
				'/': {
					name: 'goods-show',
					component: (resolve) => {
						require(['./views/index/sub/goods.vue'], resolve)
					},
				},
				'/search': {
					name: 'search',
					component: (resolve) => {
						require(['./views/index/sub/search.vue'], resolve)
					},
					subRoutes: {
						'/': {
							name: 'search-time',
							component: (resolve) => {
								require(['./views/index/sub/time.vue'], resolve)
							},
						},
						'/hot': {
							name: 'search-hot',
							component: (resolve) => {
								require(['./views/index/sub/hot.vue'], resolve)
							},
						},
						'/price': {
							name: 'search-price',
							component: (resolve) => {
								require(['./views/index/sub/price.vue'], resolve)
							},
						}
					}
				}
			}
		},
		'/login': {
			name: 'login',
			component: (resolve) => {
				require(['./views/login/login.vue'], resolve)
			},
		},
		'/register': {
			name: 'register',
			component: (resolve) => {
				require(['./views/login/register.vue'], resolve)
			},
		},
		'/advice': {
			name: 'advice',
			component: (resolve) => {
				require(['./views/login/advice.vue'], resolve)
			}
		},
		'/pwd': {
			name: 'pwd',
			component: (resolve) => {
				require(['./views/login/pwd.vue'], resolve)
			}
		},
		'numberauth': {
			name: 'numberauth',
			component: (resolve) => {
				require(['./views/login/numberauth.vue'], resolve)
			}
		},
		// 个人中心
		'/user': {
			name: 'user',
			component: (resolve) => {
				require(['./views/user/app.vue'], resolve)
			},
			subRoutes: {
				'/': {
					name: 'user-data',
					component: (resolve) => {
						require(['./views/user/sub/data.vue'], resolve)
					},
				},
				'/publish': {
					name: 'user-publish',
					component: (resolve) => {
						require(['./views/user/sub/publish.vue'], resolve)
					},
				},
				'/favorite': {
					name: 'user-favorite',
					component: (resolve) => {
						require(['./views/user/sub/favorite.vue'], resolve)
					},
				},
				'/msg': {
					name: 'user-msg',
					component: (resolve) => {
						require(['./views/user/sub/msg.vue'], resolve)
					},
				}
			}
		},
		// 协议
		'/agreement': {
			name: 'agreement',
			component: (resolve) => {
				require(['./views/agreement/app.vue'], resolve)
			},
		},
	})
}


